.missing-items-container {
  width: 550px;
  margin: auto;
  .missing-items-content {
    font-size: 14px;
    background: white;
    box-shadow: 0px 0px 5px #efefef;
    padding: 15px 25px;
    margin-bottom: 15px;
    .stock-details {
      float: right;
    }
    .field-container {
      padding: 5px 0px;
      .label {
        font-weight: 600;
        color: #3d2277;
        padding-bottom: 5px;
      }
      .custom-dropdown {
        margin-bottom: 0px;
      }
      .dimension-input-container {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        .input-field {
          padding: 10px 10px;
          width: 100%;
          // margin-right: 10px;
        }
        .input-field-container {
          width: 100%;
          .input-field {
            padding: 10px 10px;
            width: 86%;
            // margin-right: 10px;
          }
          .input-field:last-child {
            margin-right: 0px;
          }
        }
      }
      // .dimension-input-container {
      //   display: flex;
      //   align-items: flex-start;
      //   justify-content: space-between;
      //   .input-field-container {
      //     width: 100%;
      //     // padding-right: 10px;
      //     .input-field {
      //       padding: 10px 10px;
      //       width: 86%;
      //       // margin-right: 10px;
      //     }
      //     .input-field:last-child {
      //       margin-right: 0px;
      //     }
      //   }
      // }
      label {
        height: 18px;
        span {
          // padding: 0px 10px;
          font-size: 13px;
          svg {
            font-size: 19px;
          }
        }
      }
    }
    .add-product-footer {
      padding: 12px;
      .button {
        padding: 8px;
        width: 165px;
        float: right;
        // background-color: #2e3192;
        font-size: 12px !important;
        div {
          display: flex;
          align-items: center;
          justify-content: center;
          //   svg {
          //     font-size: 18px;
          //   }
        }
      }
      .progress {
        float: right;
        height: 30px !important;
        width: 30px !important;
      }
    }
    .note {
      padding-top: 22px;
      font-size: 12px;
      .note-text {
        color: red;
        font-weight: 500;
        padding-top: 2px;
        // font-size: 12px;
      }
    }
  }
}
